<template>
  <div class="page-list">
    <div class="center-center m-b-8" v-loading="loading">
      <el-pagination
        @current-change="queryApplyInfo"
        :current-page.sync="formInline.pageNumber"
        :page-size="formInline.pageSize"
        background
        layout="pager"
        :pager-count="5"
        :total="formInline.total"
      />
    </div>
    <LayoutList :docs="list.docs" />
    <div class="center-center" v-loading="loading">
      <el-pagination
        @current-change="queryApplyInfo"
        :current-page.sync="formInline.pageNumber"
        :page-size="formInline.pageSize"
        background
        layout="pager"
        :pager-count="5"
        :total="formInline.total"
      />
    </div>
  </div>
</template>

<script>
import apiUser from "@/api/user";
export default {
  name: "HomeView",
  data() {
    return {
      show: false,
      path: this.$route.fullPath,
      loading: false,
      formInline: {
        pageNumber: 1,
        roleName: "",
        total: 0,
        pageSize: 60,
        sysType: "MGR",
      },
      list: {
        docs: [],
      },
    };
  },
  computed: {},
  methods: {
    async queryApplyInfo() {
      //this.list.nextPage
      const page = this.formInline.pageNumber || 1;
      this.loading = true;
      const [err, res] = await apiUser.queryApplyInfo(this.path, {
        page,
      });
      this.loading = false;
      if (err) return;
      this.formInline.total = res.totalDocs;
      this.formInline.pageSize = res.limit;
      //totalDocs
      let docs = res.docs;
      // if (page === 1) {
      //   docs = [...this.list.docs, ...res.docs];
      // }
      this.list = {
        ...res,
        docs,
      };
    },
  },
  created() {
    this.queryApplyInfo();
  },
  mounted() {
    this.show = true;
  },
};
</script>
<style scoped lang="scss">
.page-list {
  ::v-deep {
    .el-pager > li:last-child {
      display: none;
    }
  }
}
</style>
